import logo from './logo.svg';
import './App.css';
import TaskList from './TaskList.js';
import Bottom from './Bottom.js';


function App(props) {
  return (
    <div className="App">
      <h1>List of Things to Do</h1>
        <TaskList handleCheckChange={props.handleCheckChange} handleConfEdit={props.handleConfEdit}
                  data={props.data} showCompleted={props.showCompleted}/> <br/>
        <Bottom onItemDeleted={props.onItemDeleted} onItemAdded={props.onItemAdded}
                handleHideCompleted={props.handleHideCompleted} showCompleted={props.showCompleted}/>
    </div>
  );
}

export default App;
